import '../styles/main.css';
import './components/Header';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

document.addEventListener('DOMContentLoaded', async () => {
    const API_URL = 'https://b-api-gules.vercel.app/Kegiatan';
    const kategoriSelect = document.getElementById('kategori');
    const form = document.getElementById('add-article-form');
    const errorMessage = document.getElementById('error-message');

    // Fungsi untuk mengambil data kategori
    const fetchCategories = async () => {
        try {
            const response = await fetch(API_URL);
            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }
            const data = await response.json();
            return data.data; // Menyesuaikan dengan struktur data yang diterima
        } catch (error) {
            console.error('Error fetching categories:', error);
            return null;
        }
    };

    // Fungsi untuk mengisi dropdown kategori
    const populateCategories = (data) => {
        if (data) {
            const categories = Object.keys(data);
            categories.forEach(category => {
                const option = document.createElement('option');
                option.value = category;
                option.textContent = category;
                kategoriSelect.appendChild(option);
            });
        }
    };

    // Ambil dan tampilkan kategori
    const categoriesData = await fetchCategories();
    if (categoriesData) {
        populateCategories(categoriesData);
    } else {
        errorMessage.textContent = 'Gagal memuat kategori.';
        errorMessage.classList.add('alert', 'alert-danger');
        errorMessage.style.display = 'block';
    }

    form.addEventListener('submit', async (e) => {
        e.preventDefault();

        const tanggal = new Date().toISOString();
        const judul = document.getElementById('judul').value;
        const content = document.getElementById('content').value;
        const deskripsi = document.getElementById('deskripsi').value;
        const gambarInput = document.getElementById('gambar').files[0];
        const kategori = document.getElementById('kategori').value;

        if (!judul || !content || !deskripsi || !kategori) {
            errorMessage.textContent = 'Semua bidang harus diisi!';
            errorMessage.classList.add('alert', 'alert-danger');
            errorMessage.style.display = 'block';
            return;
        }

        const reader = new FileReader();
        reader.onloadend = async () => {
            const gambar = gambarInput ? reader.result : null;

            const articleData = {
                category: kategori,
                tanggal,
                title: judul,
                description: deskripsi,
                content,
                img: gambar,
            };

            try {
                const response = await fetch(API_URL, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify(articleData)
                });
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }
                const responseData = await response.json();
                console.log('Response Data:', responseData);
                alert('Artikel berhasil ditambahkan!');
                // Arahkan ke kegiatan.html setelah berhasil
                window.location.href = 'kegiatan.html';
            } catch (error) {
                console.error('Error adding article:', error);
                errorMessage.textContent = 'Gagal menambahkan artikel. Silakan coba lagi.';
                errorMessage.classList.add('alert', 'alert-danger');
                errorMessage.style.display = 'block';
            }
        };

        if (gambarInput) {
            reader.readAsDataURL(gambarInput);
        } else {
            reader.onloadend();
        }
    });
});
